import React, { Component } from "react";
import { connect } from "react-redux";
import { reduxForm, formValueSelector } from "redux-form";
import {
  getDataSelectedValidasiTransfer,
  getValidasiTransferCustomer,
} from "../../actions/validasiTransferCustomer";
import GlobalTabel from "../../GlobalComponent/GlobalTabel";

class FormValidasiTransferCustomer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columns: [
        {
          dataField: "tanggal",
          text: "Tanggal",
        },
        {
          dataField: "no_bon",
          text: "Nomor Bon",
        },
        {
          dataField: "rek_asal",
          text: "Rekening Asal",
        },
        {
          dataField: "rek_tujuan",
          text: "Rekening Tujuan",
        },
        {
          dataField: "saldo_rp",
          text: "Saldo RP",
        },
        {
          dataField: "keterangan",
          text: "Keterangan",
        },
        {
          dataField: "link",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            return (
              <div className="text-center">
                <button
                  type="button"
                  className="btn btn-success mr-2"
                  data-toggle="modal"
                  data-target="#primary-header-modal"
                  onClick={() =>
                    this.props.dispatch(getDataSelectedValidasiTransfer(row))
                  }
                >
                  <i className="fa fa-check-circle mr-1"></i>
                  VALIDASI
                </button>
              </div>
            );
          },
        },
      ],
      searchText: "",
    };
  }

  onChange() {
    this.props.change("cashNetto", this.props.cashNetto);
    this.props.change("transferNetto", this.props.transferNetto);
  }
  componentDidMount() {
    this.props.dispatch(getValidasiTransferCustomer());
  }

  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };
  searchList() {
    console.log(this.state.searchText);
  }

  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-lg-3">
            <div className="form-group">
              <label htmlFor="">Kriteria</label>
              <select
                className="form-control"
                id="kriteria"
                name="kriteria"
                defaultValue="tanggalJanji"
              >
                <option value="DEFAULT" key="DEFAULT">
                  Tanggal Janji
                </option>
              </select>
            </div>
          </div>
          <div className="col-lg-3">
            <div className="form-group">
              <label htmlFor="">Tanggal Janji</label>
              <input
                type="date"
                id="searchNoBon"
                onChange={this.inputChange("searchText")}
                className="form-control"
              />
            </div>
          </div>
          <div className="col-lg-1">
            <div className="form-group">
              <label htmlFor="" className="text-white">
                .
              </label>
              <button
                type="button"
                className="btn btn-primary form-control"
                onClick={() => this.searchList()}
              >
                Cari
              </button>
            </div>
          </div>
          <div className="col-md-12 col-lg-12 mt-3">
            <GlobalTabel
              keyField="no_bon"
              data={this.props.listValidasiTransfer}
              columns={this.state.columns}
              bordered={true}
              headerClasses="header-table"
            />
          </div>
        </div>
      </form>
    );
  }
}

FormValidasiTransferCustomer = reduxForm({
  form: "FormValidasiTransferCustomer",
  enableReinitialize: true,
})(FormValidasiTransferCustomer);
const selector = formValueSelector("FormValidasiTransferCustomer"); // <-- same as form name
export default connect((state) => {
  const { harga, cash, transfer } = selector(
    state,
    "harga",
    "cash",
    "transfer"
  );
  return {
    cashNetto: (parseFloat(cash) / parseFloat(harga)).toFixed(3),
    transferNetto: (parseFloat(transfer) / parseFloat(harga)).toFixed(3),
    listValidasiTransfer: state.provinsi.listValidasiTransfer,
    rekening: state.provinsi.rekening,
  };
})(FormValidasiTransferCustomer);
